export function parseFromBase64<T extends unknown>(data: string): T | undefined {
	try {
		const parsed = LZString.decompressFromBase64(data);
		if (!parsed) return undefined;
		return JSON.parse(parsed);
	} catch (e) {
		console.error(`failed to parse ${data}!`)
		return undefined;
	}
}

export function parseFromUTF16<T extends unknown>(data: string): T | undefined {
	try {
		const parsed = LZString.decompressFromUTF16(data);
		if (!parsed) return undefined;
		return JSON.parse(parsed);
	} catch (e) {
		console.error(`failed to parse ${data}!`)
		return undefined;
	}
}

export function ImportSettings(val: string): boolean {
	try {
		let oldSettings = JSON.parse(JSON.stringify(Player.LSCG));
		localStorage.setItem(`LSCG_${Player.MemberNumber}_Backup`, LZString.compressToBase64(JSON.stringify(oldSettings)));
		let parsed = parseFromBase64<SettingsModel>(val);
		if (!!parsed && !!parsed.GlobalModule) {
			if (lt(parsed.Version, LSCG_VERSION)) {
				return false;
			}
			Player.LSCG = parsed; //Object.assign(Player.LSCG, parsed);
			Player.LSCG.Version = oldSettings.Version;
			Player.LSCG.ActivityModule.stats = Object.assign({}, oldSettings.ActivityModule.stats);
			Player.LSCG.CollarModule.stats = Object.assign({}, oldSettings.CollarModule.stats);
			Player.LSCG.HypnoModule.stats = Object.assign({}, oldSettings.HypnoModule.stats);
			Player.LSCG.InjectorModule.stats = Object.assign({}, oldSettings.InjectorModule.stats);
			settingsSave(true);
			return true;
		} else
			return false;
	}
	catch (error) {
		console.error("LSCG Error on Import")
		return false;
	}
}

export function CleanDefaultsFromSettings(settings: SettingsModel) : SettingsModel {
	let newObj = JSON.parse(JSON.stringify(settings));
	Object.keys(newObj).forEach(key => {
		let defaultModule = getModule(key);
		let settingModule = (<any>newObj)[key];
		if (!!defaultModule) {
			let defaults = defaultModule.defaultSettings as any;
			_compareAndTrimObjects(defaults, settingModule);
		}
	})
	return newObj;
}

function _compareAndTrimObjects(defaults: any, settings: any) {
	if (!defaults || !settings)
		return;
	Object.keys(defaults).forEach(dk => {
		let defaultVal = defaults[dk];
		let settingVal = settings[dk];
		if (!Array.isArray(settingVal)) {
			if (typeof defaultVal === "number")
				settingVal = parseInt(settings[dk]);
			
			if (typeof settingVal === "object")
				_compareAndTrimObjects(defaultVal, settingVal);
			else if (defaultVal === settingVal)
				delete settings[dk];
		} else if (JSON.stringify(defaultVal) === JSON.stringify(settingVal)) {
			delete settings[dk];
		}
	})
}

/** Checks if the `obj` is an object (not null, not array) */
export function isObject(obj: unknown): obj is Record<string, any> {
	return !!obj && typeof obj === "object" && !Array.isArray(obj);
}

export function escapeRegExp(string: string) {
	return regEscape(string ?? "").toLocaleLowerCase();
}

export function isPhraseInString(string: string, phrase: string, ignoreOOC: boolean = false) {
	if (!string || string === "")
		return false;
	let praseMatch = new RegExp("(\\b|^|\\s)" + escapeRegExp(phrase) + "(\\b|$|\\s)", "i");
	let oocParsed = ignoreOOC ? string : excludeParentheticalContent(string);
	return praseMatch.test(oocParsed);
}

export function addCustomEffect(C: Character | null, effect: LSCGEffectName): boolean {
	if (!C)
		C = Player;
	let updated = false;
	const emoticon = C.Appearance.find((a) => a.Asset.Name === "Emoticon");
	if (!emoticon) {
		return updated;
	}

	const allowEffect = (emoticon.Asset.AllowEffect as LSCGEffectName[]) ??= [];
	allowEffect.push(effect);

	if (!emoticon.Property) {
		emoticon.Property = {};
		updated = true;
	}
	if (!emoticon.Property.Effect) {
		emoticon.Property.Effect = [];
		updated = true;
	}
	const propEffect = (emoticon.Property.Effect as LSCGEffectName[]);
	if (!propEffect.includes(effect)) {
		propEffect.push(effect);
		updated = true;
	}

	if (updated && ServerPlayerIsInChatRoom() && C == Player) {
		ChatRoomCharacterUpdate(Player);
		//CharacterRelease(Player, true);
	}
	return updated;
}

export function removeCustomEffect(C: Character | null, effect: LSCGEffectName): boolean  {
	if (!C)
		C = Player;
	const emoticon = C.Appearance.find((a) => a.Asset.Name === "Emoticon");
	let updated = false;
	
	const propEffect = (emoticon?.Property?.Effect ?? []) as LSCGEffectName[];
	const effIndex = propEffect.indexOf(effect);
	if (effIndex !== -1) {
		propEffect.splice(effIndex, 1);
		updated = true;
	}
	if (updated && ServerPlayerIsInChatRoom() && C == Player) {
		ChatRoomCharacterUpdate(Player);
	}
	return updated;
}

