export function GetHandheldItemNameAndDescriptionConcat(C?: Character | null): string | undefined {
	if (!C)
		C = Player;

	var asset = InventoryGet(C, "ItemHandheld");
	return GetItemNameAndDescriptionConcat(asset);
}	

export function GetItemNameAndDescriptionConcat(item: Item | ItemBundle | null): string | undefined {
	if (!item || !item.Craft)
		return;
	
	var name = item.Craft.Name;
	var description = typeof CraftingDescription === "undefined" ? item.Craft.Description : CraftingDescription.Decode(item.Craft.Description); // R109
	return name + " | " + description;
}

export function mouseTooltip(msg: string, x?: number, y?: number, maxX?: number, maxY?: number) {
	var prevAlign = MainCanvas.textAlign;
	var prevFont = MainCanvas.font;
	var pad = 5;
	MainCanvas.textAlign = "left";
	MainCanvas.font = '16px Arial, sans-serif'
	var size = MainCanvas.measureText(msg);
	let width = size.actualBoundingBoxRight - size.actualBoundingBoxLeft + 2 * pad;
	var TextX = Math.max(0, Math.min(maxX ?? 1000, (x ?? MouseX) + width)) - width;
	var TextY = Math.max(0, Math.min(maxY ?? 1000, y ?? MouseY));
	DrawRect(TextX - pad + 3, TextY - size.actualBoundingBoxAscent - pad + 3, width, size.actualBoundingBoxDescent + size.actualBoundingBoxAscent + 2 * pad, "rgba(0, 0, 0, .7)");
	DrawRect(TextX - pad, TextY - size.actualBoundingBoxAscent - pad, width, size.actualBoundingBoxDescent + size.actualBoundingBoxAscent + 2 * pad, "#D7F6E9");
	DrawTextFit(msg, TextX, TextY, size.width, "Black");
	MainCanvas.textAlign = prevAlign;
	MainCanvas.font = prevFont;
}

export function toItemBundle(item: Item, character: Character | undefined): ItemBundle {
    const asset = AssetGet(character?.AssetFamily ?? "Female3DCG", item.Asset.Group.Name, item.Asset.Name);
    if (asset == null) {
        throw new Error(`Unknown asset: ${item.Asset.Group.Name}${item.Asset.Name}`);
    }
    return <ItemBundle>{
        Group: item.Asset.Group.Name,
        Name: item.Asset.Name,
        Color: clone(item.Color),
        Craft: clone(item.Craft),
        Property: clone(item.Property)
    };
}

export function fromItemBundle(bundle: ItemBundle): Item | undefined {
	let asset = AssetGet(Player.AssetFamily ?? "Female3DCG", bundle.Group, bundle.Name);
	if (!asset) return undefined;
	return {
		Asset: asset,
		Color: bundle.Color,
		Craft: bundle.Craft,
		Difficulty: bundle.Difficulty,
		Property: bundle.Difficulty
	} as Item
}

function measureDataSize(data: any) {
    try {
        if (typeof data !== "string") {
            data = JSON.stringify(data) || "";
        }
        if (typeof data === "string") {
            // We want byte size, so use text encoder
            return (new TextEncoder()).encode(data).byteLength;
        }
    } catch (_err) {
        // Ignore errors
    }
    // If stringification fails, we can't guess how the data would be actually serialized
    return 0;
}

// Data Measuring Function from Joshmir
export function GetDataSizeReport(data: any, log: boolean = true): number {
    let res = `Type: ${data == null ? "null" : Array.isArray(data) ? "array" : typeof data}\n`;
	let totalSize = measureDataSize(data);
    res += `Size: ${totalSize}\n`;

    if (typeof data === "string" && data.length < 64) {
        res += `Value: ${JSON.stringify(data)}\n`;
    }

    if (!!data && typeof data === "object" && !Array.isArray(data)) {
        res += "Breakdown by keys:\n";
        for (const [key, value] of Object.entries(data).sort((a, b) => measureDataSize(b[1]) - measureDataSize(a[1]))) {
            res += `- ${key}: ${measureDataSize(value)}\n`;
        }
    }

	if (log)
    	console.log(res);
	return totalSize;
}

export function stringIsCompressedItemBundleArray(str: string): boolean {
	try {
		return Array.isArray(parseFromBase64(str))
	}
	catch {
		return false;
	}
}

export function GetConfiguredItemBundlesFromOutfitKey(key: string, filter: (item: ItemBundle) => boolean): ItemBundle[] {
	let outfits = getModule<OutfitCollectionModule>("OutfitCollectionModule")?.data;
	let items = outfits.GetOutfitBundle(key);
	if (!items || items.length <= 0) {
		if (stringIsCompressedItemBundleArray(key)) {
			items = parseFromBase64<ItemBundle[]>(key) ?? [];
		} else if (CommonIsNumeric(key) && !!Player.Wardrobe) {
			let ix = parseInt(key);
			if (ix >= 0 && ix < Player.Wardrobe.length)
				items = Player.Wardrobe[ix];
		} else if (key.length <= 70 && typeof mbs !== "undefined") {
			items = mbs.wheelOutfits.getByName(key)?.items ?? [];
		}
	}
	
	return items.filter(item => filter(item));
}

